let injectionRateSlider,
	injectionRateValueInput,
	dynamicHeadSlider,
	dynamicHeadValueInput,
	powerRateSlider,
	powerRateValueInput,
	efficiencySlider,
	efficiencyValueInput,
	dutyCycleSlider,
	dutyCycleValueInput,
	modelObj;


function createSlider(selector) {
	return new mercurySlider({
		container: selector,
		validation: {
			enabled: false
		}
	})
}

function createSliderValueInput(selector) {
	return new numberInput({
		container: selector,
		validation: {
			enabled: false
		}
	})
}

function bindInputs(slider, valueInput) {
	slider.container.addEventListener("input", (e) => {
		valueInput.val(slider.val());
		updateModelObj();
	})

	valueInput.container.addEventListener("keyup", (e) => {
		slider.val(valueInput.val());
		updateModelObj();
	})
}

function updateModelObj() {
	modelObj.injectionRate = injectionRateValueInput.val();
	modelObj.totalDynamicHead = dynamicHeadValueInput.val();
	modelObj.powerRate = powerRateValueInput.val();
	modelObj.efficieny = efficiencyValueInput.val();
	modelObj.dutyCyclePerYear = dutyCycleValueInput.val();

	modelObj.calcEnergyRecoveryCosts();
	updateOutputsHTML();
}

function updateOutputsHTML() {

	const $energyRecoveryCostsHourly = document.querySelector("#energy-recovery-costs-hourly-value");

	if ( isNaN(modelObj.energyRecoveryCosts) ) {
		$energyRecoveryCostsHourly.textContent = "Check slider values";
	} else {
		$energyRecoveryCostsHourly.textContent = '$' + Number(modelObj.hourlyCost).toLocaleString('en', {maximumFractionDigits : 2}) + ' per hour';
	}

	const $energyRecoveryCosts = document.querySelector("#energy-recovery-costs-value");

	if ( isNaN(modelObj.energyRecoveryCosts) ) {
		$energyRecoveryCosts.textContent = "Check slider values";
	} else {
		$energyRecoveryCosts.textContent = '$' + Number(modelObj.energyRecoveryCosts).toLocaleString('en', {maximumFractionDigits : 2}) + ' per duty cycle';
	}

	const $rechargeVolume = document.querySelector("#recharge-volume-value");

	if ( isNaN(modelObj.energyRecoveryCosts) ) {
		$rechargeVolume.textContent = "Check slider values";
	} else {
		$rechargeVolume.textContent = Number(modelObj.afDutyCycle).toLocaleString('en', {maximumFractionDigits : 2}) + ' AF';
	}
}

modelObj = {
	injectionRate: null,
	totalDynamicHead: null,
	powerRate: null,
	efficieny: null,
	dutyCyclePerYear: null,
	hourlyCost: null,
	hoursDutyCycle: null,
	energyRecoveryCosts: null,
	gallonsDutyCycle: null,
	afDutyCycle: null,
	calcHourlyCost: function() {
		this.hourlyCost = Number((this.injectionRate * this.totalDynamicHead  * 0.746 * this.powerRate)/(3960 * this.efficieny));
	},
	calcHoursDutyCycle: function() {
		this.hoursDutyCycle = Number((365 * this.dutyCyclePerYear)*24);
		this.calcGallonsDutyCycle();
	},
	calcEnergyRecoveryCosts: function() {
		this.calcHourlyCost();
		this.calcHoursDutyCycle();
		this.energyRecoveryCosts = Number(this.hourlyCost * this.hoursDutyCycle);
	},
	calcGallonsDutyCycle: function() {
		this.gallonsDutyCycle = Number(this.injectionRate * 60 * this.hoursDutyCycle);
		this.calcAfDutyCycle();
	},
	calcAfDutyCycle: function() {
		this.afDutyCycle = Number(this.gallonsDutyCycle*0.0000030688);
	}
}

injectionRateSlider = createSlider("#injection-rate-slider");
injectionRateValueInput = createSliderValueInput("#injection-rate-slider-value");

dynamicHeadSlider = createSlider("#dynamic-head-slider");
dynamicHeadValueInput = createSliderValueInput("#dynamic-head-slider-value");

powerRateSlider = createSlider("#power-rate-slider");
powerRateValueInput = createSliderValueInput("#power-rate-slider-value");

efficiencySlider = createSlider("#efficiency-slider");
efficiencyValueInput = createSliderValueInput("#efficiency-slider-value");

dutyCycleSlider = createSlider("#duty-cycle-slider");
dutyCycleValueInput = createSliderValueInput("#duty-cycle-slider-value");

injectionRateSlider.val(1000);
injectionRateValueInput.val(1000);
dynamicHeadSlider.val(100);
dynamicHeadValueInput.val(100);
powerRateSlider.val(0.07);
powerRateValueInput.val(0.07);
efficiencySlider.val(0.7);
efficiencyValueInput.val(0.7);
dutyCycleSlider.val(0.8);
dutyCycleValueInput.val(0.8);
updateModelObj();


const bindEvents = () => {
	bindInputs(injectionRateSlider, injectionRateValueInput);
	bindInputs(dynamicHeadSlider, dynamicHeadValueInput);
	bindInputs(powerRateSlider, powerRateValueInput);
	bindInputs(efficiencySlider, efficiencyValueInput);
	bindInputs(dutyCycleSlider, dutyCycleValueInput);
}
bindEvents();